.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;

  .header & {
    padding-top: 46px;
    padding-bottom: 97px;
  }

  @media screen and (min-width: 320px) {
    max-width: 320px;
  }

  @media screen and (min-width:768px) {
    max-width: 768px;
    padding: 0 71px;
  }

  @media screen and (min-width:1024px) {
    max-width: 1024px;
    padding: 0 71px;
  }
}
